const SHOP_STORAGE_EVENT = 'shop-storage-update';
const hasWindow = typeof window !== 'undefined';

export const CART_STORAGE_KEY = 'shopCart';
export const FAVORITES_STORAGE_KEY = 'shopFavorites';
export const SAVED_ADDRESS_STORAGE_KEY = 'shopSavedAddress';
export const SEARCH_HISTORY_STORAGE_KEY = 'shopSearchHistory';

export const readJSONStorage = (key, fallback = null) => {
  if (!hasWindow) {
    return fallback;
  }
  try {
    const raw = window.localStorage.getItem(key);
    if (raw === null || raw === undefined) {
      return fallback;
    }
    const parsed = JSON.parse(raw);
    return parsed ?? fallback;
  } catch (error) {
    console.error(`Failed to read storage key "${key}":`, error);
    return fallback;
  }
};

export const writeJSONStorage = (key, value) => {
  if (!hasWindow) {
    return;
  }
  try {
    if (value === undefined) {
      window.localStorage.removeItem(key);
    } else {
      window.localStorage.setItem(key, JSON.stringify(value));
    }
    window.dispatchEvent(new CustomEvent(SHOP_STORAGE_EVENT, { detail: { key } }));
  } catch (error) {
    console.error(`Failed to write storage key "${key}":`, error);
  }
};

export const subscribeToStorageKey = (key, callback) => {
  if (!hasWindow || typeof callback !== 'function') {
    return () => {};
  }

  const handleUpdate = (event) => {
    if (event.type === 'storage' && event.key !== key) {
      return;
    }
    if (event.type === 'storage' || (event.type === SHOP_STORAGE_EVENT && event.detail?.key === key)) {
      callback(readJSONStorage(key));
    }
  };

  window.addEventListener('storage', handleUpdate);
  window.addEventListener(SHOP_STORAGE_EVENT, handleUpdate);

  return () => {
    window.removeEventListener('storage', handleUpdate);
    window.removeEventListener(SHOP_STORAGE_EVENT, handleUpdate);
  };
};

export const clearStorageKey = (key) => writeJSONStorage(key, []);

