import { useEffect, useState } from 'react';
import { apiBaseUrl } from '../config';

const useStaticPages = (context) => {
  const [pages, setPages] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    let isMounted = true;
    const fetchPages = async () => {
      if (!context) return;
      try {
        setLoading(true);
        const response = await fetch(`${apiBaseUrl}/api/static-pages?context=${context}`);
        if (!response.ok) {
          throw new Error('Failed to fetch static pages');
        }
        const data = await response.json();
        if (isMounted) {
          setPages(data.pages || []);
        }
      } catch (error) {
        console.error(`Error loading ${context} static pages:`, error);
        if (isMounted) {
          setPages([]);
        }
      } finally {
        if (isMounted) {
          setLoading(false);
        }
      }
    };
    fetchPages();
    return () => {
      isMounted = false;
    };
  }, [context]);

  return { pages, loading };
};

export default useStaticPages;

