import React, { useEffect, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';
import {
  CART_STORAGE_KEY,
  SAVED_ADDRESS_STORAGE_KEY,
  readJSONStorage,
  subscribeToStorageKey,
  writeJSONStorage
} from '../utils/shopStorage';

const resolveImage = (image) => {
  if (!image) return null;
  if (image.startsWith('http')) return image;
  return `${apiBaseUrl}${image}`;
};

const formatCurrency = (value) => {
  if (value == null || Number.isNaN(Number(value))) {
    return '0';
  }
  return `${Number(value).toLocaleString()}`;
};

const ShopCartPage = () => {
  const navigate = useNavigate();
  const [cartItems, setCartItems] = useState(() => readJSONStorage(CART_STORAGE_KEY, []));
  const [savedAddress, setSavedAddress] = useState(() => readJSONStorage(SAVED_ADDRESS_STORAGE_KEY, null));
  const [isProcessing, setIsProcessing] = useState(false);

  useEffect(() => {
    const unsubscribe = subscribeToStorageKey(CART_STORAGE_KEY, (items) => {
      setCartItems(Array.isArray(items) ? items : []);
    });
    return unsubscribe;
  }, []);

  useEffect(() => {
    const unsubscribe = subscribeToStorageKey(SAVED_ADDRESS_STORAGE_KEY, (value) => {
      setSavedAddress(value || null);
    });
    return unsubscribe;
  }, []);

  const updateCart = (nextItems) => {
    const normalized = typeof nextItems === 'function' ? nextItems(cartItems) : nextItems;
    const safeList = Array.isArray(normalized) ? normalized : [];
    setCartItems(safeList);
    writeJSONStorage(CART_STORAGE_KEY, safeList);
  };

  const subtotal = useMemo(() => {
    return cartItems.reduce((total, item) => {
      const price = Number(item.discountedPrice ?? item.price ?? item.originalPrice ?? 0);
      return total + price * (item.quantity || 1);
    }, 0);
  }, [cartItems]);

  const savings = useMemo(() => {
    return cartItems.reduce((total, item) => {
      const original = Number(item.originalPrice ?? item.price ?? 0);
      const current = Number(item.discountedPrice ?? item.price ?? original);
      if (original > current) {
        return total + (original - current) * (item.quantity || 1);
      }
      return total;
    }, 0);
  }, [cartItems]);

  const handleQuantityChange = (itemId, delta) => {
    updateCart((items) =>
      items.map((item) => {
        if (item.id === itemId) {
          const nextQty = Math.max(1, (item.quantity || 1) + delta);
          return { ...item, quantity: nextQty };
        }
        return item;
      })
    );
  };

  const handleRemove = (itemId) => {
    updateCart((items) => items.filter((item) => item.id !== itemId));
  };

  const handleCheckout = () => {
    if (!cartItems.length) {
      return;
    }
    setIsProcessing(true);
    const grouped = cartItems.map((item) => ({
      id: item.id,
      quantity: item.quantity || 1
    }));
    localStorage.setItem('shopCheckoutQueue', JSON.stringify(grouped));
    const first = grouped[0];
    navigate(`/shop/checkout/${first.id}?quantity=${first.quantity}&batch=true`);
  };

  const handleSingleCheckout = (item) => {
    navigate(`/shop/checkout/${item.id}?quantity=${item.quantity || 1}`);
  };

  const renderAddress = () => {
    if (!savedAddress) return null;
    const { fullName, phone, addressLine1, addressLine2, city, state, postcode } = savedAddress;
    return (
      <div className="shop-cart-address">
        <div className="shop-cart-address-header">
          <p>Shipping to</p>
          <button className="text-btn" onClick={() => navigate('/profile-edit')}>
            Update
          </button>
        </div>
        {fullName && <strong>{fullName}</strong>}
        <p>
          {[addressLine1, addressLine2].filter(Boolean).join(', ')}
        </p>
        <p>
          {[city, state, postcode].filter(Boolean).join(', ')}
        </p>
        {phone && <p>Phone: {phone}</p>}
      </div>
    );
  };

  return (
    <div className="shop-cart-page page-with-bottom-nav">
      <header className="shop-cart-header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M19 12H5M12 19l-7-7 7-7" />
          </svg>
        </button>
        <div>
          <h1>My Cart</h1>
          <p>{cartItems.length} {cartItems.length === 1 ? 'item' : 'items'} saved</p>
        </div>
        {cartItems.length > 0 ? (
          <button className="text-btn" onClick={() => updateCart([])}>Clear all</button>
        ) : (
          <div style={{ width: 60 }}></div>
        )}
      </header>

      {!cartItems.length ? (
        <div className="shop-cart-empty">
          <div className="empty-icon"></div>
          <h2>Your cart is waiting</h2>
          <p>Add items to your cart and they will show up here for quick checkout.</p>
          <button className="primary-btn" onClick={() => navigate('/shop')}>
            Browse Products
          </button>
        </div>
      ) : (
        <>
          <div className="shop-cart-list">
            {cartItems.map((item) => (
              <div key={item.id} className="shop-cart-card">
                <div className="shop-cart-card-media">
                  {item.image ? (
                    <img src={resolveImage(item.image)} alt={item.title} />
                  ) : (
                    <div className="image-placeholder"></div>
                  )}
                </div>
                <div className="shop-cart-card-info">
                  <div className="shop-cart-card-header">
                    <h3>{item.title}</h3>
                    <button className="icon-only-btn" aria-label="Remove item" onClick={() => handleRemove(item.id)}>
                      <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                        <line x1="18" y1="6" x2="6" y2="18"></line>
                        <line x1="6" y1="6" x2="18" y2="18"></line>
                      </svg>
                    </button>
                  </div>
                  <p className="shop-cart-meta">
                    {item.category && <span>{item.category}</span>}
                    {item.seller && <span>Seller: {item.seller}</span>}
                  </p>
                  <div className="shop-cart-price-row">
                    <strong>{formatCurrency(item.discountedPrice ?? item.price ?? item.originalPrice)}</strong>
                    {item.originalPrice && item.discountedPrice && item.originalPrice > item.discountedPrice && (
                      <span className="strike-price">{formatCurrency(item.originalPrice)}</span>
                    )}
                  </div>
                  <div className="shop-cart-actions">
                    <div className="quantity-controls">
                      <button onClick={() => handleQuantityChange(item.id, -1)}>-</button>
                      <span>{item.quantity || 1}</span>
                      <button onClick={() => handleQuantityChange(item.id, 1)}>+</button>
                    </div>
                    <button className="text-btn" onClick={() => handleSingleCheckout(item)}>
                      Buy this
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>

          <div className="shop-cart-summary">
            <div className="summary-card">
              <div className="summary-row">
                <span>Subtotal</span>
                <strong>{formatCurrency(subtotal)}</strong>
              </div>
              <div className="summary-row">
                <span>Savings</span>
                <span className="savings-text">- {formatCurrency(savings)}</span>
              </div>
              <div className="summary-row total">
                <span>Estimated total</span>
                <strong>{formatCurrency(subtotal)}</strong>
              </div>
              <div className="summary-actions">
                <button className="secondary-btn" onClick={() => navigate('/shop')}>
                  Continue Shopping
                </button>
                <button
                  className="primary-btn"
                  onClick={handleCheckout}
                  disabled={isProcessing || !cartItems.length}
                >
                  {isProcessing ? 'Opening checkout...' : 'Checkout all'}
                </button>
              </div>
            </div>
            {renderAddress()}
          </div>
        </>
      )}
    </div>
  );
};

export default ShopCartPage;

