import { useCallback, useEffect, useMemo, useState } from 'react';
import {
  FAVORITES_STORAGE_KEY,
  readJSONStorage,
  writeJSONStorage
} from '../utils/shopStorage';

// Simple function to format product for storage
const formatProductForFavorites = (product) => {
  if (!product || !product.id) return null;

  // Get primary image - handle both string and array formats
  let primaryImage = null;
  if (product.image) {
    primaryImage = product.image;
  } else if (Array.isArray(product.images) && product.images.length > 0) {
    primaryImage = product.images[0];
  }

  // Get prices
  const original = product.original_price ?? product.originalPrice ?? product.price ?? 0;
  const discounted = product.discounted_price ?? product.discountedPrice ?? null;
  const priceToUse = discounted ?? original;

  return {
    id: Number(product.id),
    title: product.title || 'Untitled Product',
    price: Number(priceToUse) || 0,
    originalPrice: Number(original) || Number(priceToUse) || 0,
    discountedPrice: discounted ? Number(discounted) : null,
    image: primaryImage, // Store image URL as-is
    category: product.category_name || product.category || product.subcategory_name || '',
    provider: product.provider_name || product.provider || 'Admin',
    isAdminProduct: Boolean(product.is_admin_product || product.isAdminProduct)
  };
};

const useShopFavorites = () => {
  // Initialize from localStorage
  const [favorites, setFavorites] = useState(() => {
    try {
      const stored = readJSONStorage(FAVORITES_STORAGE_KEY, []);
      return Array.isArray(stored) ? stored : [];
    } catch (error) {
      console.error('Error reading favorites from storage:', error);
      return [];
    }
  });

  // Listen for storage changes (e.g., from other tabs)
  useEffect(() => {
    const handleStorageChange = (e) => {
      if (e.key === FAVORITES_STORAGE_KEY) {
        try {
          const stored = readJSONStorage(FAVORITES_STORAGE_KEY, []);
          setFavorites(Array.isArray(stored) ? stored : []);
        } catch (error) {
          console.error('Error reading favorites from storage change:', error);
        }
      }
    };

    window.addEventListener('storage', handleStorageChange);
    return () => window.removeEventListener('storage', handleStorageChange);
  }, []);

  const isFavorite = useCallback(
    (productId) => favorites.some((item) => Number(item.id) === Number(productId)),
    [favorites]
  );

  // Toggle favorite - add if not exists, remove if exists
  const toggleFavorite = useCallback((product) => {
    if (!product || !product.id) return;
    
    try {
      const normalized = formatProductForFavorites(product);
      if (!normalized) return;

      setFavorites((prev) => {
        const exists = prev.some((item) => Number(item.id) === Number(normalized.id));
        const next = exists 
          ? prev.filter((item) => Number(item.id) !== Number(normalized.id))
          : [...prev, normalized];
        
        // Save to localStorage
        writeJSONStorage(FAVORITES_STORAGE_KEY, next);
        return next;
      });
    } catch (error) {
      console.error('Error toggling favorite:', error);
    }
  }, []);

  // Remove a favorite by product ID
  const removeFavorite = useCallback((productId) => {
    setFavorites((prev) => {
      const next = prev.filter((item) => Number(item.id) !== Number(productId));
      writeJSONStorage(FAVORITES_STORAGE_KEY, next);
      return next;
    });
  }, []);

  // Clear all favorites
  const clearFavorites = useCallback(() => {
    setFavorites([]);
    writeJSONStorage(FAVORITES_STORAGE_KEY, []);
  }, []);

  const favoriteIds = useMemo(() => new Set(favorites.map((item) => item.id)), [favorites]);

  return {
    favorites,
    favoriteIds,
    isFavorite,
    toggleFavorite,
    removeFavorite,
    clearFavorites
  };
};

export default useShopFavorites;

