import React from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';
import useShopFavorites from '../hooks/useShopFavorites';

const formatCurrency = (value) => {
  if (value == null) return '0';
  return `${Number(value).toLocaleString()}`;
};

// Resolve image URL - add API base URL if it's a relative path
const resolveImageUrl = (image) => {
  if (!image) return null;
  // If already a full URL, return as-is
  if (image.startsWith('http://') || image.startsWith('https://')) {
    return image;
  }
  // If it's a relative path, prepend the API base URL
  // Handle both with and without leading slash
  if (image.startsWith('/')) {
    return `${apiBaseUrl}${image}`;
  }
  return `${apiBaseUrl}/${image}`;
};

const ShopFavoritesPage = () => {
  const navigate = useNavigate();
  const { favorites, toggleFavorite, clearFavorites } = useShopFavorites();

  const handleCardClick = (productId) => {
    navigate(`/shop/product/${productId}`);
  };

  return (
    <div className="shop-favorites-page page-with-bottom-nav">
      <header className="shop-favorites-header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M19 12H5M12 19l-7-7 7-7" />
          </svg>
        </button>
        <div>
          <h1>Saved items</h1>
          <p>{favorites.length} {favorites.length === 1 ? 'product' : 'products'}</p>
        </div>
        {favorites.length > 0 ? (
          <button className="text-btn" onClick={clearFavorites}>Clear</button>
        ) : (
          <div style={{ width: 48 }}></div>
        )}
      </header>

      {favorites.length === 0 ? (
        <div className="shop-favorites-empty">
          <div className="empty-icon"></div>
          <h2>No favourites yet</h2>
          <p>Tap the heart icon on any product to save it to this list for quick access later.</p>
          <button className="primary-btn" onClick={() => navigate('/shop')}>
            Explore products
          </button>
        </div>
      ) : (
        <div className="shop-favorites-grid">
          {favorites.map((item) => (
            <div key={item.id} className="favorite-card" onClick={() => handleCardClick(item.id)}>
              <button
                className="favorite-card-remove"
                aria-label="Remove from favourites"
                onClick={(event) => {
                  event.stopPropagation();
                  toggleFavorite(item);
                }}
              >
                <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                  <path d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"/>
                </svg>
              </button>
              <div className="favorite-card-media">
                {item.image ? (
                  <img 
                    src={resolveImageUrl(item.image)} 
                    alt={item.title}
                    onError={(e) => {
                      e.target.style.display = 'none';
                      if (e.target.nextSibling) {
                        e.target.nextSibling.style.display = 'flex';
                      }
                    }}
                  />
                ) : null}
                <div className="image-placeholder" style={{ display: item.image ? 'none' : 'flex' }}></div>
              </div>
              <div className="favorite-card-body">
                <p className="favorite-card-category">{item.category || 'General'}</p>
                <h3>{item.title}</h3>
                <div className="favorite-card-price">
                  <strong>{formatCurrency(item.discountedPrice ?? item.price ?? item.originalPrice)}</strong>
                  {item.originalPrice && item.discountedPrice && item.originalPrice > item.discountedPrice && (
                    <span className="strike-price">{formatCurrency(item.originalPrice)}</span>
                  )}
                </div>
                <p className="favorite-card-provider">{item.provider}</p>
                <button className="secondary-btn" onClick={(event) => {
                  event.stopPropagation();
                  handleCardClick(item.id);
                }}>
                  View product
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default ShopFavoritesPage;

