import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { apiBaseUrl } from '../config';

const StaticPage = () => {
  const { slug } = useParams();
  const [page, setPage] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchPage = async () => {
      if (!slug) return;
      try {
        setLoading(true);
        setError(null);
        const response = await fetch(`${apiBaseUrl}/api/static-pages/${slug}`);
        if (!response.ok) {
          const data = await response.json().catch(() => ({}));
          throw new Error(data.error || 'Page not found');
        }
        const data = await response.json();
        setPage(data);
      } catch (err) {
        setPage(null);
        setError(err.message || 'Failed to load page');
      } finally {
        setLoading(false);
      }
    };
    fetchPage();
  }, [slug]);

  const renderWrapper = (children) => (
    <div
      className="static-page-wrapper page-with-bottom-nav"
      style={{
        padding: '20px',
        background: 'linear-gradient(135deg, #f8f9fa 0%, #e0e7ff 100%)',
        minHeight: '100vh'
      }}
    >
      <div
        className="static-page-card"
        style={{
          background: '#fff',
          borderRadius: '16px',
          padding: '20px',
          boxShadow: '0 10px 30px rgba(15, 23, 42, 0.1)',
          maxWidth: '900px',
          margin: '0 auto'
        }}
      >
        {children}
      </div>
    </div>
  );

  if (loading) {
    return renderWrapper(<p>Loading page...</p>);
  }

  if (error) {
    return renderWrapper(
      <>
        <h2>Oops!</h2>
        <p>{error}</p>
      </>
    );
  }

  if (!page) {
    return null;
  }

  const formattedContent = page.content
    ? page.content.replace(/\n/g, '<br />')
    : 'No content provided.';

  return renderWrapper(
    <>
      <h1 style={{ marginBottom: '16px' }}>{page.title}</h1>
      <div
        className="static-page-content"
        style={{ lineHeight: 1.6, color: '#374151' }}
        dangerouslySetInnerHTML={{ __html: formattedContent }}
      />
    </>
  );
};

export default StaticPage;

