const REFERRER_CODE_STORAGE_KEY = 'uh_referrer_code';

const getWindowSafe = () => (typeof window !== 'undefined' ? window : undefined);

export const buildReferralCode = (user) => {
  if (!user?.id) {
    return '';
  }
  const safeName = user.name
    ? user.name
        .trim()
        .toLowerCase()
        .replace(/[^a-z0-9\s-]/g, '')
        .replace(/\s+/g, '-')
    : '';
  return safeName ? `${user.id}-${safeName}` : `${user.id}`;
};

export const buildReferralLink = (user) => {
  const referralCode = buildReferralCode(user);
  if (!referralCode) {
    return '';
  }
  const win = getWindowSafe();
  const origin = win?.location?.origin || 'https://b.unlimitedhub.in';
  return `${origin}/login?ref=${encodeURIComponent(referralCode)}`;
};

export const storeIncomingReferrerCode = (code) => {
  if (!code) {
    return;
  }
  try {
    const trimmed = String(code).trim();
    if (!trimmed) return;
    const win = getWindowSafe();
    win?.localStorage?.setItem(REFERRER_CODE_STORAGE_KEY, trimmed);
  } catch (error) {
    console.warn('Unable to store referrer code:', error);
  }
};

export const getStoredReferrerCode = () => {
  try {
    const win = getWindowSafe();
    return win?.localStorage?.getItem(REFERRER_CODE_STORAGE_KEY) || '';
  } catch (error) {
    console.warn('Unable to read referrer code:', error);
    return '';
  }
};

export const clearStoredReferrerCode = () => {
  try {
    const win = getWindowSafe();
    win?.localStorage?.removeItem(REFERRER_CODE_STORAGE_KEY);
  } catch (error) {
    console.warn('Unable to clear referrer code:', error);
  }
};

