import React, { useEffect, useState } from 'react';
import { apiBaseUrl } from '../config';

const defaultFormState = {
  id: null,
  title: '',
  slug: '',
  content: '',
  show_in_calls: false,
  show_in_ads: false,
  show_in_shop: false,
  is_active: true
};

const AdminStaticPages = () => {
  const [pages, setPages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [form, setForm] = useState(defaultFormState);
  const [notification, setNotification] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');

  const showMessage = (type, message) => {
    setNotification({ type, message });
    setTimeout(() => setNotification(null), 5000);
  };

  const fetchPages = async () => {
    try {
      setLoading(true);
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/static-pages`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      if (!response.ok) {
        throw new Error('Failed to load static pages');
      }
      const data = await response.json();
      setPages(data.pages || []);
    } catch (error) {
      console.error('Error fetching static pages:', error);
      showMessage('error', error.message || 'Failed to load pages');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchPages();
  }, []);

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const handleEdit = (page) => {
    setForm({
      id: page.id,
      title: page.title,
      slug: page.slug,
      content: page.content,
      show_in_calls: page.show_in_calls,
      show_in_ads: page.show_in_ads,
      show_in_shop: page.show_in_shop,
      is_active: page.is_active
    });
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const handleReset = () => {
    setForm(defaultFormState);
  };

  const handleDelete = async (pageId) => {
    if (!window.confirm('Are you sure you want to delete this page?')) {
      return;
    }
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/static-pages/${pageId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      if (!response.ok) {
        throw new Error('Failed to delete page');
      }
      showMessage('success', 'Page deleted successfully');
      fetchPages();
      if (form.id === pageId) {
        handleReset();
      }
    } catch (error) {
      console.error('Error deleting static page:', error);
      showMessage('error', error.message || 'Failed to delete page');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!form.title.trim()) {
      showMessage('error', 'Title is required');
      return;
    }
    setSaving(true);
    try {
      const payload = {
        title: form.title.trim(),
        slug: form.slug.trim() || form.title.trim().toLowerCase().replace(/[^a-z0-9]+/g, '-').replace(/^-|-$/g, ''),
        content: form.content,
        show_in_calls: form.show_in_calls,
        show_in_ads: form.show_in_ads,
        show_in_shop: form.show_in_shop,
        is_active: form.is_active
      };
      const token = localStorage.getItem('adminToken');
      const url = form.id
        ? `${apiBaseUrl}/api/admin/static-pages/${form.id}`
        : `${apiBaseUrl}/api/admin/static-pages`;
      const method = form.id ? 'PUT' : 'POST';
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(payload)
      });
      const data = await response.json();
      if (!response.ok) {
        throw new Error(data.error || 'Failed to save page');
      }
      showMessage('success', data.message || 'Page saved successfully');
      handleReset();
      fetchPages();
    } catch (error) {
      console.error('Error saving static page:', error);
      showMessage('error', error.message || 'Failed to save page');
    } finally {
      setSaving(false);
    }
  };

  const filteredPages = pages.filter(page => 
    page.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
    page.slug.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="static-pages-admin">
      {notification && (
        <div className={`static-pages-notification static-pages-notification-${notification.type}`}>
          <div className="static-pages-notification-content">
            <span className="static-pages-notification-icon">
              {notification.type === 'success' ? '' : ''}
            </span>
            <span>{notification.message}</span>
          </div>
        </div>
      )}

      <div className="static-pages-card">
        <div className="static-pages-card-header">
          <div className="static-pages-card-header-content">
            <div className="static-pages-card-icon">
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path>
                <polyline points="14 2 14 8 20 8"></polyline>
                <line x1="16" y1="13" x2="8" y2="13"></line>
                <line x1="16" y1="17" x2="8" y2="17"></line>
                <polyline points="10 9 9 9 8 9"></polyline>
              </svg>
            </div>
            <div>
              <h3>{form.id ? 'Edit Static Page' : 'Create New Static Page'}</h3>
              <p>{form.id ? 'Update the static page details below' : 'Fill in the details to create a new static page'}</p>
            </div>
          </div>
          {form.id && (
            <button className="static-pages-btn-secondary" onClick={handleReset}>
              <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <line x1="18" y1="6" x2="6" y2="18"></line>
                <line x1="6" y1="6" x2="18" y2="18"></line>
              </svg>
              Cancel
            </button>
          )}
        </div>
        <form onSubmit={handleSubmit} className="static-pages-form">
          <div className="static-pages-form-grid">
            <div className="static-pages-form-group static-pages-form-group-full">
              <label>
                <span className="static-pages-label-text">Title</span>
                <span className="static-pages-required">*</span>
              </label>
              <input
                type="text"
                name="title"
                value={form.title}
                onChange={handleInputChange}
                placeholder="e.g. Refund Policy"
                className="static-pages-input"
                required
              />
            </div>
            
            <div className="static-pages-form-group static-pages-form-group-full">
              <label>
                <span className="static-pages-label-text">Slug</span>
                <span className="static-pages-label-hint">(Optional)</span>
              </label>
              <input
                type="text"
                name="slug"
                value={form.slug}
                onChange={handleInputChange}
                placeholder="e.g. refund-policy"
                className="static-pages-input"
              />
              <small className="static-pages-input-hint">
                Leave blank to auto-generate from title
              </small>
            </div>
            
            <div className="static-pages-form-group static-pages-form-group-full">
              <label>
                <span className="static-pages-label-text">Content</span>
              </label>
              <textarea
                name="content"
                value={form.content}
                onChange={handleInputChange}
                rows="8"
                placeholder="Write the content for this page. You can use HTML formatting..."
                className="static-pages-textarea"
              />
            </div>
            
            <div className="static-pages-form-group static-pages-form-group-full">
              <label className="static-pages-label-text">Visibility Settings</label>
              <div className="static-pages-checkbox-group">
                <label className="static-pages-checkbox-label">
                  <input
                    type="checkbox"
                    name="show_in_calls"
                    checked={form.show_in_calls}
                    onChange={handleInputChange}
                    className="static-pages-checkbox"
                  />
                  <span className="static-pages-checkbox-custom"></span>
                  <span className="static-pages-checkbox-text">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                      <path d="M22 16.92v3a2 2 0 0 1-2.18 2 19.79 19.79 0 0 1-8.63-3.07 19.5 19.5 0 0 1-6-6 19.79 19.79 0 0 1-3.07-8.67A2 2 0 0 1 4.11 2h3a2 2 0 0 1 2 1.72 12.84 12.84 0 0 0 .7 2.81 2 2 0 0 1-.45 2.11L8.09 9.91a16 16 0 0 0 6 6l1.27-1.27a2 2 0 0 1 2.11-.45 12.84 12.84 0 0 0 2.81.7A2 2 0 0 1 22 16.92z"></path>
                    </svg>
                    Show in Calls
                  </span>
                </label>
                <label className="static-pages-checkbox-label">
                  <input
                    type="checkbox"
                    name="show_in_ads"
                    checked={form.show_in_ads}
                    onChange={handleInputChange}
                    className="static-pages-checkbox"
                  />
                  <span className="static-pages-checkbox-custom"></span>
                  <span className="static-pages-checkbox-text">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                      <rect x="3" y="3" width="18" height="18" rx="2" ry="2"></rect>
                      <line x1="3" y1="9" x2="21" y2="9"></line>
                      <line x1="9" y1="21" x2="9" y2="9"></line>
                    </svg>
                    Show in Ads
                  </span>
                </label>
                <label className="static-pages-checkbox-label">
                  <input
                    type="checkbox"
                    name="show_in_shop"
                    checked={form.show_in_shop}
                    onChange={handleInputChange}
                    className="static-pages-checkbox"
                  />
                  <span className="static-pages-checkbox-custom"></span>
                  <span className="static-pages-checkbox-text">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                      <circle cx="9" cy="21" r="1"></circle>
                      <circle cx="20" cy="21" r="1"></circle>
                      <path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"></path>
                    </svg>
                    Show in Shop
                  </span>
                </label>
              </div>
            </div>
            
            <div className="static-pages-form-group">
              <label className="static-pages-label-text">Status</label>
              <label className="static-pages-switch-label">
                <input
                  type="checkbox"
                  name="is_active"
                  checked={form.is_active}
                  onChange={handleInputChange}
                  className="static-pages-switch-input"
                />
                <span className="static-pages-switch-slider"></span>
                <span className="static-pages-switch-text">
                  {form.is_active ? 'Active' : 'Inactive'}
                </span>
              </label>
            </div>
          </div>
          
          <div className="static-pages-form-actions">
            <button
              type="submit"
              className="static-pages-btn-primary"
              disabled={saving}
            >
              {saving ? (
                <>
                  <svg className="static-pages-spinner" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M21 12a9 9 0 11-6.219-8.56"></path>
                  </svg>
                  Saving...
                </>
              ) : (
                <>
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M19 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h11l5 5v11a2 2 0 0 1-2 2z"></path>
                    <polyline points="17 21 17 13 7 13 7 21"></polyline>
                    <polyline points="7 3 7 8 15 8"></polyline>
                  </svg>
                  {form.id ? 'Update Page' : 'Create Page'}
                </>
              )}
            </button>
          </div>
        </form>
      </div>

      <div className="static-pages-card">
        <div className="static-pages-card-header">
          <div className="static-pages-card-header-content">
            <div className="static-pages-card-icon">
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M4 19.5A2.5 2.5 0 0 1 6.5 17H20"></path>
                <path d="M6.5 2H20v20H6.5A2.5 2.5 0 0 1 4 19.5v-15A2.5 2.5 0 0 1 6.5 2z"></path>
              </svg>
            </div>
            <div>
              <h3>Existing Pages</h3>
              <p>{pages.length} {pages.length === 1 ? 'page' : 'pages'} total</p>
            </div>
          </div>
          <div className="static-pages-card-header-actions">
            <div className="static-pages-search-wrapper">
              <svg className="static-pages-search-icon" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <circle cx="11" cy="11" r="8"></circle>
                <path d="m21 21-4.35-4.35"></path>
              </svg>
              <input
                type="text"
                placeholder="Search pages..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="static-pages-search-input"
              />
            </div>
            <button className="static-pages-btn-secondary" onClick={fetchPages} disabled={loading}>
              <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <polyline points="23 4 23 10 17 10"></polyline>
                <polyline points="1 20 1 14 7 14"></polyline>
                <path d="M3.51 9a9 9 0 0 1 14.85-3.36L23 10M1 14l4.64 4.36A9 9 0 0 0 20.49 15"></path>
              </svg>
              {loading ? 'Refreshing...' : 'Refresh'}
            </button>
          </div>
        </div>
        <div className="static-pages-card-body">
          {loading && pages.length === 0 ? (
            <div className="static-pages-empty-state">
              <svg width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M21 12a9 9 0 11-6.219-8.56"></path>
              </svg>
              <p>Loading pages...</p>
            </div>
          ) : filteredPages.length === 0 ? (
            <div className="static-pages-empty-state">
              <svg width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path>
                <polyline points="14 2 14 8 20 8"></polyline>
                <line x1="16" y1="13" x2="8" y2="13"></line>
                <line x1="16" y1="17" x2="8" y2="17"></line>
              </svg>
              <p>{searchQuery ? 'No pages found matching your search' : 'No static pages found'}</p>
              <p className="static-pages-empty-state-hint">
                {searchQuery ? 'Try a different search term' : 'Create one using the form above'}
              </p>
            </div>
          ) : (
            <div className="static-pages-table-wrapper">
              <table className="static-pages-table">
                <thead>
                  <tr>
                    <th>Title</th>
                    <th>Slug</th>
                    <th>Visible In</th>
                    <th>Status</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {filteredPages.map(page => (
                    <tr key={page.id} className={form.id === page.id ? 'static-pages-row-editing' : ''}>
                      <td>
                        <div className="static-pages-table-title">
                          <strong>{page.title}</strong>
                        </div>
                      </td>
                      <td>
                        <code className="static-pages-slug">{page.slug}</code>
                      </td>
                      <td>
                        <div className="static-pages-visibility-tags">
                          {page.show_in_calls && (
                            <span className="static-pages-tag static-pages-tag-calls">
                              <svg width="12" height="12" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                <path d="M22 16.92v3a2 2 0 0 1-2.18 2 19.79 19.79 0 0 1-8.63-3.07 19.5 19.5 0 0 1-6-6 19.79 19.79 0 0 1-3.07-8.67A2 2 0 0 1 4.11 2h3a2 2 0 0 1 2 1.72 12.84 12.84 0 0 0 .7 2.81 2 2 0 0 1-.45 2.11L8.09 9.91a16 16 0 0 0 6 6l1.27-1.27a2 2 0 0 1 2.11-.45 12.84 12.84 0 0 0 2.81.7A2 2 0 0 1 22 16.92z"></path>
                              </svg>
                              Calls
                            </span>
                          )}
                          {page.show_in_ads && (
                            <span className="static-pages-tag static-pages-tag-ads">
                              <svg width="12" height="12" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                <rect x="3" y="3" width="18" height="18" rx="2" ry="2"></rect>
                                <line x1="3" y1="9" x2="21" y2="9"></line>
                                <line x1="9" y1="21" x2="9" y2="9"></line>
                              </svg>
                              Ads
                            </span>
                          )}
                          {page.show_in_shop && (
                            <span className="static-pages-tag static-pages-tag-shop">
                              <svg width="12" height="12" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                <circle cx="9" cy="21" r="1"></circle>
                                <circle cx="20" cy="21" r="1"></circle>
                                <path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"></path>
                              </svg>
                              Shop
                            </span>
                          )}
                          {!page.show_in_calls && !page.show_in_ads && !page.show_in_shop && (
                            <span className="static-pages-tag static-pages-tag-muted">
                              <svg width="12" height="12" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                <path d="M18 6L6 18M6 6l12 12"></path>
                              </svg>
                              Hidden
                            </span>
                          )}
                        </div>
                      </td>
                      <td>
                        <span className={`static-pages-status-badge ${page.is_active ? 'static-pages-status-active' : 'static-pages-status-inactive'}`}>
                          <span className="static-pages-status-dot"></span>
                          {page.is_active ? 'Active' : 'Inactive'}
                        </span>
                      </td>
                      <td>
                        <div className="static-pages-action-buttons">
                          <button 
                            className="static-pages-btn-action static-pages-btn-edit" 
                            onClick={() => handleEdit(page)}
                            title="Edit page"
                          >
                            <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                              <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                              <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
                            </svg>
                            Edit
                          </button>
                          <button 
                            className="static-pages-btn-action static-pages-btn-delete" 
                            onClick={() => handleDelete(page.id)}
                            title="Delete page"
                          >
                            <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                              <polyline points="3 6 5 6 21 6"></polyline>
                              <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                            </svg>
                            Delete
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default AdminStaticPages;

