import React, { useEffect, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';
import {
  SEARCH_HISTORY_STORAGE_KEY,
  readJSONStorage,
  subscribeToStorageKey,
  writeJSONStorage
} from '../utils/shopStorage';
import useShopFavorites from '../hooks/useShopFavorites';

const formatCurrency = (value) => {
  if (value == null) return '0';
  return `${Number(value).toLocaleString()}`;
};

const suggestedQueries = ['Shoes', 'Jewelry', 'Accessories', 'Gadgets', 'Kids'];

const ShopSearchPage = () => {
  const navigate = useNavigate();
  const { isFavorite, toggleFavorite } = useShopFavorites();
  const [query, setQuery] = useState('');
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [history, setHistory] = useState(() => readJSONStorage(SEARCH_HISTORY_STORAGE_KEY, []));

  useEffect(() => {
    const unsubscribe = subscribeToStorageKey(SEARCH_HISTORY_STORAGE_KEY, (items) => {
      setHistory(Array.isArray(items) ? items : []);
    });
    return unsubscribe;
  }, []);

  const persistHistory = (next) => {
    setHistory(next);
    writeJSONStorage(SEARCH_HISTORY_STORAGE_KEY, next);
  };

  const addToHistory = (term) => {
    const clean = term.trim();
    if (!clean) return;
    persistHistory([clean, ...history.filter((entry) => entry !== clean)].slice(0, 6));
  };

  const performSearch = async (term) => {
    const clean = term.trim();
    if (!clean) {
      setResults([]);
      setError(null);
      return;
    }
    setLoading(true);
    setError(null);
    try {
      const response = await fetch(`${apiBaseUrl}/api/products?search=${encodeURIComponent(clean)}`);
      if (!response.ok) {
        throw new Error('Failed to load products');
      }
      const data = await response.json();
      setResults(Array.isArray(data.products) ? data.products : []);
      addToHistory(clean);
    } catch (err) {
      console.error('Search failed:', err);
      setError(err.message || 'Unable to search right now.');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    performSearch(query);
  };

  const handleHistoryClick = (term) => {
    setQuery(term);
    performSearch(term);
  };

  const handleClearHistory = () => {
    persistHistory([]);
  };

  const resultCountLabel = useMemo(() => {
    if (loading) return 'Searching...';
    if (results.length === 0 && !error && query.trim()) return 'No matches';
    if (!query.trim()) return 'Try searching for products';
    return `${results.length} results`;
  }, [loading, results.length, error, query]);

  return (
    <div className="shop-search-page page-with-bottom-nav">
      <header className="shop-search-header">
        <button className="back-btn" onClick={() => navigate(-1)}>
          <svg width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M19 12H5M12 19l-7-7 7-7" />
          </svg>
        </button>
        <div className="shop-search-title">
          <h1>Search shop</h1>
          <p>{resultCountLabel}</p>
        </div>
      </header>

      <form className="shop-search-bar" onSubmit={handleSubmit}>
        <div className="search-input-wrapper">
          <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <circle cx="11" cy="11" r="8"></circle>
            <path d="m21 21-4.35-4.35"></path>
          </svg>
          <input
            type="search"
            placeholder="Search for product name, category, keywords..."
            value={query}
            onChange={(event) => setQuery(event.target.value)}
          />
        </div>
        <button className="primary-btn" type="submit" disabled={loading}>
          {loading ? 'Searching...' : 'Search'}
        </button>
      </form>

  <div className="shop-search-suggestions">
        <div className="suggestions-row">
          {suggestedQueries.map((term) => (
            <button key={term} className="suggestion-chip" onClick={() => handleHistoryClick(term)}>
              {term}
            </button>
          ))}
        </div>
        {history.length > 0 && (
          <div className="search-history">
            <div className="history-header">
              <p>Recent searches</p>
              <button className="text-btn" onClick={handleClearHistory}>Clear</button>
            </div>
            <div className="history-chips">
              {history.map((term) => (
                <button key={term} className="history-chip" onClick={() => handleHistoryClick(term)}>
                  {term}
                </button>
              ))}
            </div>
          </div>
        )}
      </div>

      {error && (
        <div className="error-container">
          <p>{error}</p>
        </div>
      )}

      <div className="shop-search-results">
        {results.map((product) => {
          const price = product.discounted_price ?? product.original_price;
          return (
            <div key={product.id} className="search-card" onClick={() => navigate(`/shop/product/${product.id}`)}>
              <div className="search-card-media">
                {product.images && product.images.length > 0 ? (
                  <img
                    src={product.images[0].startsWith('http') ? product.images[0] : `${apiBaseUrl}${product.images[0]}`}
                    alt={product.title}
                  />
                ) : (
                  <div className="image-placeholder"></div>
                )}
                <button
                  className={`product-favorite-btn ${isFavorite(product.id) ? 'active' : ''}`}
                  aria-label="Toggle favourite"
                  onClick={(event) => {
                    event.stopPropagation();
                    toggleFavorite(product);
                  }}
                >
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"/>
                  </svg>
                </button>
              </div>
              <div className="search-card-body">
                <p className="search-card-category">{product.category_name}</p>
                <h3>{product.title}</h3>
                <p className="search-card-description">
                  {product.description?.length > 90 ? `${product.description.substring(0, 90)}...` : product.description}
                </p>
                <div className="search-card-price">
                  <strong>{formatCurrency(price)}</strong>
                  {product.original_price && product.discounted_price && (
                    <span className="strike-price">{formatCurrency(product.original_price)}</span>
                  )}
                </div>
                <p className="search-card-provider">Seller: {product.provider_name || 'Admin'}</p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default ShopSearchPage;

